import { Component, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { DataService } from '../../services/data.service'
import { Log } from '../../models/data'
import { Subscription, timer } from 'rxjs'
import { switchMap } from 'rxjs/operators';


@Component({
    selector: 'logs',
    templateUrl: './logs.component.html',
    styleUrls: ['./logs.component.less']
})
export class LogsComponent implements OnDestroy {

    log: Log

    // Used to scroll to bottom
    @ViewChild('scrollPane') private myScrollContainer: ElementRef;

    /* Section for unsubscribing from Subscriptions to avoid
    memory leaks */
    subscription: Subscription = new Subscription()

    /**
     * Constructor for this logs component
     * @param dataService DataService to retrieve logs
     */
    constructor(private dataService: DataService) { }

    /**
     * Gets the logs and scrolsl to bottom
     */
    ngOnInit() : void {
        // Gets logs every 10s
        this.subscription = timer(0,10000).pipe(
            switchMap(() => this.dataService.getLogs())).subscribe(result => { this.log = result, this.scrollToBottom()})
        
    }

    scrollToBottom(): void {
        try {
            this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
        } catch (err) { }
    }

    ngOnDestroy() : void {
        this.subscription.unsubscribe()
    }

    //TODO: Scroll to bottom doesnt work lol
}
